import React from "react"
import { connect } from "react-redux"
import { Helmet } from 'react-helmet';
import { Card, CardBody, CardHeader, CardTitle, Row, Col } from "reactstrap"
import Breadcrumbs from "../../../../components/@vuexy/breadCrumbs/BreadCrumb"
import * as API from "../../../../utility/crud/app.crud"
import * as Date from "../../../../utility/dateAndTime";
import SystemStatusTable from "./Tables/SystemStatusTable"
import SystemStatusModals from "./Modals/SystemStatusModals";
import { toastr } from 'react-redux-toastr'

class SystemStatus extends React.Component {
    constructor(props) {
        super(props)

        this.state = {
            columns: [],
            data: [],
            filteredData: [],
            value: '',
            ID: 0,
            STATUSNAME: '',
            CATEGORY: '',
            CREATEDBYID: 1,
            LASTMODIFIEDBYID: 1,
            DATECREATED: Date.ShowDate(),
            DATELASTMODIFIED: Date.ShowDate(),
            InsertUpdate: ''
        }

        this.handleShow = () => {
            this.setState({
                show: true,
                InsertUpdate: 'Insert'
            });
            this.clearData();
        }

        this.handleHide = () => {
            this.setState({
                show: false
            });
        }

        this.handleHideDelete = () => {
            this.setState({
                showdelete: false
            });
        }

    }

    componentDidMount() {
        this.populateData();
    }

    populateData() {
        const getItemsQuery = `
        query {
          SystemStatus {
              ID
              STATUSNAME
              CATEGORY
              CREATEDBYID
              LASTMODIFIEDBYID
              DATECREATED
              DATELASTMODIFIED
          }
        }
      `;
  
          API.getData(getItemsQuery)
              .then(({ data }) => {
  
                  let items = data.data.SystemStatus;
                  let rowData = [];
  
                  items.forEach(item => {
  
                      rowData.push({
                          ID: item.ID,
                          STATUSNAME: item.STATUSNAME,
                          CATEGORY: item.CATEGORY
                      })
  
                  });
  
                  this.setState({ data: rowData })
              })
              .catch(error => {
                  console.error(error);
              })
    }

    handleSubmit = (event) => {
        event.preventDefault();

        const form = `
        mutation{
            addSystemStatus(
                STATUSNAME:"${this.state.STATUSNAME}",
                CATEGORY:${this.state.CATEGORY},
                CREATEDBYID:${this.state.CREATEDBYID},
                LASTMODIFIEDBYID:${this.state.LASTMODIFIEDBYID},
                DATECREATED:"${this.state.DATECREATED}",
                DATELASTMODIFIED:"${this.state.DATELASTMODIFIED}"
                ){
                ID
            }
        }
        `;


        const formEdit = `
        mutation{
            updateSystemStatus(
                ID:${this.state.ID},
                STATUSNAME:"${this.state.STATUSNAME}",
                CATEGORY:${this.state.CATEGORY},
                LASTMODIFIEDBYID:${this.state.LASTMODIFIEDBYID},
                DATELASTMODIFIED:"${this.state.DATELASTMODIFIED}"
                ){
                ID
            }
        }
        `;

        if (this.state.InsertUpdate === 'Insert') {
            API.create(form)
                .then(() => {
                    this.clearData();
                    this.handleHide();
                    this.populateData();
                    toastr.success('Create Record', 'Record Created Successfully');
                }).catch(error => {
                    if (error.response || error.request) {
                        toastr.error('Error Loading Data', 'Failed to Load Data');
                    }
                })
        } else {
            API.update(formEdit)
                .then(() => {
                    this.clearData();
                    this.handleHide();
                    this.populateData();
                    toastr.info('Update Record', 'Record Updated Successfully');
                }).catch(error => {
                    if (error.response || error.request) {
                        toastr.error('Error Loading Data', 'Failed to Load Data');
                    }
                })
        }
    }

    handleDelete = (event) => {
        event.preventDefault();

        const formDelete = `
        mutation{
            deleteSystemStatus(
                ID:${this.state.ID}
                ){
                ID
            }
        }
        `;

        API.remove(formDelete)
            .then(() => {
                this.handleHideDelete();
                this.populateData();
                toastr.warning('Delete Record', 'Record Deleted Successfully');
            }).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Data');
                }
            })

    }

    handleEditData = (e) => {
        this.setState({
            ID: e.ID,
            STATUSNAME: e.STATUSNAME,
            CATEGORY: e.CATEGORY,
            show: true,
            InsertUpdate: 'Update'
        })
    }

    handleDeleteData = (e) => {
        this.setState({
            ID: e.ID,
            STATUSNAME: e.STATUSNAME,
            showdelete: true,
            InsertUpdate: 'Delete'
        })
    }

    clearData() {
        this.setState({
            STATUSNAME: '',
            CATEGORY: ''
        })
    }

    handleFilter = (e) => {
        let value = e.target.value
        let data = this.state.data
        let filteredData = this.state.filteredData
        this.setState({ value });

        if (value.length) {
            filteredData = data.filter(item => {
                let startsWithCondition =
                    item.STATUSNAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.CATEGORY.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.STATUSNAME.toString().toLowerCase().includes(value.toLowerCase()) ||
                    item.CATEGORY.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredData })
        }
    }

    render() {
        const { data, columns, value, filteredData } = this.state
        return (
            <>
            <Helmet><title>System Status</title></Helmet>
            <React.Fragment>
                <Breadcrumbs
                    breadCrumbTitle="Admin"
                    breadCrumbParent="Setup"
                    breadCrumbActive="System Status"
                />
                <Row>
                    <Col sm="12">
                        <Card>
                            <CardHeader>
                                <CardTitle>System Status</CardTitle>
                            </CardHeader>
                            <CardBody className="rdt_Wrapper">
                                
                                <SystemStatusTable
                                    handleEdit={this.handleEditData}
                                    handleDelete={this.handleDeleteData}
                                    value={value}
                                    data={data}
                                    columns={columns}
                                    filteredData={filteredData}
                                    handleShow={this.handleShow}
                                    handleFilter={this.handleFilter}
                                />

                                <SystemStatusModals
                                    show={this.state.show}
                                    handleHide={this.handleHide}
                                    InsertUpdate={this.state.InsertUpdate}
                                    handleSubmit={this.handleSubmit}
                                    StatusName={this.state.STATUSNAME}
                                    changeStatusName={e => this.setState({ STATUSNAME: e.target.value })}
                                    Category={this.state.CATEGORY}
                                    changeCategory={e => this.setState({ CATEGORY: e.target.value })}
                                    showdelete={this.state.showdelete}
                                    handleHideDelete={this.handleHideDelete}
                                    handleDelete={this.handleDelete}
                                />


                            </CardBody>
                        </Card>
                    </Col>
                </Row>
            </React.Fragment>
            </>
            
        )
    }
}

const mapStateToProps = state => {
    return {
        user: state.auth.user
    }
}

export default connect(mapStateToProps)(SystemStatus)

