import React from "react"
import { Button } from "reactstrap"
import DataTable from "react-data-table-component"
import TableCustomHeader from "../../../MainDataTable/TableCustomHeader";
import { Edit, Trash2 } from "react-feather"

class SystemStatusTable extends React.Component {

    tableProperties = {
        columns: [
            {
                name: "Action",
                cell: (row) => (
                    <>
                        <Button.Ripple className="mr-1 mb-1 bg-gradient-success" size="sm" color="none"
                            onClick={() => this.props.handleEdit(row)}
                        ><Edit size={14} />
                        </Button.Ripple>
                        <Button.Ripple className="mr-1 mb-1  bg-gradient-danger" size="sm" color="none"
                            onClick={() => this.props.handleDelete(row)}
                        ><Trash2 size={14} />
                        </Button.Ripple>
                    </>
                )
            },
            {
                name: "Status Name",
                selector: "STATUSNAME",
                sortable: true,
                cell: row => <p className="mb-0">{row.STATUSNAME}</p>
            },
            {
                name: "Category",
                selector: "CATEGORY",
                sortable: true,
                cell: row => <p className="mb-0">{row.CATEGORY}</p>
            }
        ]
    }

    render() {

        return (
            <>
            <DataTable
                className="dataTable-custom"
                data={this.props.value.length ? this.props.filteredData : this.props.data}
                columns={this.tableProperties.columns}
                noHeader
                pagination
                subHeader
                striped
                subHeaderComponent={
                    <TableCustomHeader value={this.props.value} handleShow={this.props.handleShow} handleFilter={this.props.handleFilter} />

                }
            />
            </>

        )
    }
}


export default SystemStatusTable

